  top: 25vh !important;
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}
.scene-Title h1 {
    font-size: 5vw;
    color: #fff;
    letter-spacing: 20px;
    position: absolute;
    top: 37vh;
    left: 34vw;
}
